/* LOANS*/

.top-btns {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
}

.top-btns > .select {
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 30px;
    width: 100%;
    background-color: var(--main-color);
}

select:hover {
    cursor: pointer;
}

select {
    height: 2rem;
    border-radius: 10px;
    padding: 7px;
    width: 100%;
    height: 100%;
}

.top-btns .acept-btn {
    background-color: #393e46;
    border: none;
    color: white;
    padding: 15px 32px;
    font-size: 16px;
    border-radius: 10px;
}

.top-btns .acept-btn:hover {
    background-color: #f7c873;
    transition: 0.3s;
}

.top-btns .cancel-btn {
    background-color: #393e46;
    border: none;
    border-radius: 10px;
    color: white;
    padding: 15px 32px;
    font-size: 16px;
    width: 100%;
    margin-top: 1rem;
}

.top-btns .cancel-btn:hover {
    background-color: #f7c873;
    transition: 0.3s;
}


/* LOANS CALCULATOR */

.loans-calculator {
    display: flex;
    justify-content: center;
    align-items: center;
}

.input-boxes {
    list-style: none;
    padding: 0;
    margin: 0;
}

.input-boxes > label {
    margin-top: 10px;
}

.calculator-c {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:var(--main-color);
    text-decoration: none;
    width: 100%;
    margin-bottom: 30px;
    border-radius: 30px;
}


.result {
    color: var(--white-color);
    text-align: center;
    margin: 30px 0px 0px 0px;
}

.input-boxes {
    color: var(--white-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.span {
    color: var(--white-color);
    position: absolute;
}


.resultText {
    font-weight: 400;
    font-size: 20px;
}